    padding: 0;
    margin: 16px;
    box-shadow: 0 3px 8px 0 rgba(0, 0, 0, .3);
    border-radius: 50%;
    width: 32px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    outline: none;
    background-color: #fff;
    border: none
}

.rcw-previewer-container .rcw-previewer-close-button {
    position: absolute;
    right: 0;
    top: 0
}

.rcw-previewer-container .rcw-previewer-veil {
    width: 100%;
    height: 100%;
    overflow: scroll;
    position: relative
}

@-webkit-keyframes rotation-lr {
    0% {
        transform: rotate(-90deg)
    }

    to {
        transform: rotate(0)
    }
}

@keyframes rotation-lr {
    0% {
        transform: rotate(-90deg)
    }

    to {
        transform: rotate(0)
    }
}

@-webkit-keyframes rotation-rl {
    0% {
        transform: rotate(90deg)
    }

    to {
        transform: rotate(0)
    }
}

@keyframes rotation-rl {
    0% {
        transform: rotate(90deg)
    }

    to {
        transform: rotate(0)
    }
}

@-webkit-keyframes slide-in {
    0% {
        opacity: 0;
        transform: translateY(10px)
    }

    to {
        opacity: 1;
        transform: translateY(0)
    }
}

@keyframes slide-in {
    0% {
        opacity: 0;
        transform: translateY(10px)
    }

    to {
        opacity: 1;
        transform: translateY(0)
    }
}

@-webkit-keyframes slide-out {
    0% {
        opacity: 1;
        transform: translateY(0)
    }

    to {
        opacity: 0;
        transform: translateY(10px)
    }
}

@keyframes slide-out {
    0% {
        opacity: 1;
        transform: translateY(0)
    }

    to {
        opacity: 0;
        transform: translateY(10px)
    }
}

.rcw-widget-container {
    bottom: 0;
    display: flex;
    flex-direction: column;
    margin: 0 20px 20px 0;
    position: fixed;
    right: 0;
    z-index: 9999
}

.rcw-full-screen {
    height: 100vh;
    margin: 0;
    max-width: none;
    width: 100%
}

@media screen and (max-width:800px) {
    .rcw-widget-container {
        height: 100%;
        height: 100vh;
        margin: 0;
        max-width: none;
        width: 100%
    }
}

.rcw-previewer .rcw-message-img {
    cursor: pointer
}

.rcw-close-widget-container {
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content
}